import * as React from "react";
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "./Card";
import { Button } from "./Button";
import { UserProvider } from "../typings/user";
import { Separator } from "./Separator";
import { cn } from "../utils";
import { useBedrockPassport } from "../hooks/useBedrockPassport";
import { Icons } from "./Icons";
import WalletLink from "./WalletLink";
import { defaultFeatures, FeatureConfig } from "../config/features";
import { EmailLogin } from "./EmailLogin";
import { useState } from "react";
import { EmailRegistration } from "./EmailRegister";
import { EmailReset } from "./EmailReset";
import { CurrencyPanel } from "./Currency";

interface LoginProps {
  panelClass?: string;
  headerClass?: string;
  buttonClass?: string;
  title?: string;
  titleClass?: string;
  logo?: string;
  logoClass?: string;
  logoAlt?: string;
  showConnectWallet?: boolean;
  walletButtonClass?: string;
  walletButtonText?: string;
  separatorClass?: string;
  separatorText?: string;
  separatorTextClass?: string;
  features?: FeatureConfig;
  linkRowClass?: string;
}

export const LoginPanel: React.FC<LoginProps> = ({ features, ...props }) => {
  const featureFlags = { ...defaultFeatures, ...features };

  const {
    signIn,
    user,
    linkSocial,
    widgetText,
    widgetUrl,
    signOut,
    linkProfile,
    isLoggedIn,
  } = useBedrockPassport();
  const [showEmailForm, setShowEmailForm] = useState(false);
  const [showRegistrationForm, setShowRegistrationForm] = useState(false);
  const [showResetForm, setShowResetForm] = useState(false);

  const loginWGoogle = () => {
    if (user?.ethAddress) {
      linkSocial(UserProvider.GOOGLE);
    } else {
      signIn(UserProvider.GOOGLE);
    }
  };

  const loginWApple = () => {
    if (user?.ethAddress) {
      linkSocial(UserProvider.APPLE);
    } else {
      signIn(UserProvider.APPLE);
    }
  };

  if (
    !featureFlags.enableAppleLogin &&
    !featureFlags.enableGoogleLogin &&
    !featureFlags.enableEmailLogin &&
    !featureFlags.enableWalletConnect
  ) {
    return (
      <Card className={cn("min-w-60", props.panelClass)}>
        <CardHeader>
          <div className={cn("flex items-center", props.headerClass)}>
            <CardTitle className={cn(props.titleClass)}>
              {props.title ?? "Sign In to"}
            </CardTitle>
            {props.logo && (
              <img
                src={props.logo}
                alt={props.logoAlt}
                className={cn(props.logoClass)}
              />
            )}
          </div>
        </CardHeader>
        <CardContent className="grid gap-4">
          You will need to enable at least one login method
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className={cn("min-w-60", props.panelClass)}>
      <CardHeader>
        <div className={cn("flex items-center", props.headerClass)}>
          <CardTitle className={cn(props.titleClass)}>
            {props.title ?? "Sign In to"}
          </CardTitle>
          {props.logo && (
            <img
              src={props.logo}
              alt={props.logoAlt}
              className={cn(props.logoClass)}
            />
          )}
        </div>
      </CardHeader>
      <CardContent className="grid gap-4">
        {user?.email ? (
          <>
            <div className="flex flex-row gap-4 justify-center items-center">
              {user.provider === UserProvider.APPLE ? (
                <Button variant="outline" disabled>
                  <Icons.apple />
                </Button>
              ) : (
                <Button variant="outline" disabled>
                  <Icons.google />
                </Button>
              )}
              <div>{user?.email}</div>
            </div>
            <div
              className={cn(
                "flex justify-start items-center w-full gap-2",
                props.linkRowClass,
              )}
            >
              {featureFlags.showProfileButton && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, "")}
                  onClick={linkProfile}
                >
                  Profile
                </Button>
              )}
              {featureFlags.showLogoutButton && (
                <Button
                  variant="outline"
                  onClick={signOut}
                  className={cn(props.buttonClass, "")}
                >
                  Logout
                </Button>
              )}
            </div>
            {featureFlags.enableCurrencyDisplay && (
              <div
                className={cn(
                  props.linkRowClass,
                  " flex justify-start items-center w-full gap-2",
                )}
              >
                <CurrencyPanel />
              </div>
            )}
          </>
        ) : !showEmailForm && !showRegistrationForm && !showResetForm ? (
          <div className="flex flex-col gap-4">
            <h4 className="text-white text-center font-bold text-sm">
              Continue with
            </h4>

            <div className="grid auto-cols-fr grid-flow-col gap-2 ">
              {featureFlags.enableGoogleLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass, "")}
                  onClick={() => loginWGoogle()}
                >
                  <Icons.google />
                </Button>
              )}
              {featureFlags.enableAppleLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass)}
                  onClick={() => loginWApple()}
                >
                  <Icons.apple />
                </Button>
              )}
              {featureFlags.enableEmailLogin && (
                <Button
                  variant="outline"
                  className={cn(props.buttonClass)}
                  onClick={() => setShowEmailForm(true)}
                >
                  <Icons.email />
                </Button>
              )}
            </div>
          </div>
        ) : (
          <>
            {showRegistrationForm && (
              <EmailRegistration buttonClass={props.buttonClass} />
            )}
            {showEmailForm && <EmailLogin buttonClass={props.buttonClass} />}

            {showResetForm && (
              <EmailReset
                buttonClass={props.buttonClass}
                separatorClass={props.separatorTextClass}
              />
            )}
          </>
        )}
        {showEmailForm || showRegistrationForm || showResetForm ? (
          <>
            <div className="relative">
              <Separator
                className={cn("my-4", "bg-white", props.separatorClass)}
                orientation="horizontal"
              />
              {!isLoggedIn && (
                <span
                  className={cn(
                    "absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 flex justify-center items-center py-1 px-2",
                    "bg-black",
                    props.separatorTextClass,
                  )}
                >
                  {props.separatorText ?? "or"}
                </span>
              )}
            </div>
            <div className="flex items-center justify-center w-full gap-2">
              <Button
                type="button"
                variant="outline"
                className={cn(props.buttonClass)}
                onClick={() => {
                  setShowEmailForm(false);
                  setShowRegistrationForm(false);
                  setShowResetForm(false);
                }}
              >
                Back
              </Button>

              <Button
                type="button"
                variant="outline"
                className={cn(props.buttonClass)}
                onClick={() => {
                  if (showResetForm) {
                    setShowEmailForm(false);
                    setShowRegistrationForm(true);
                    setShowResetForm(false);
                    return;
                  }

                  setShowEmailForm(!showEmailForm);
                  setShowRegistrationForm(!showRegistrationForm);
                }}
              >
                {showRegistrationForm ? "Sign In" : "Register"}
              </Button>
              {!showResetForm && (
                <Button
                  type="button"
                  variant="outline"
                  className={cn(props.buttonClass)}
                  onClick={() => {
                    setShowEmailForm(false);
                    setShowRegistrationForm(false);
                    setShowResetForm(true);
                  }}
                >
                  Reset
                </Button>
              )}
            </div>
          </>
        ) : (
          <>
            {featureFlags.enableWalletConnect && props.showConnectWallet && (
              <>
                {(featureFlags.enableAppleLogin ||
                  featureFlags.enableGoogleLogin ||
                  featureFlags.enableEmailLogin) && (
                  <div className="relative">
                    <Separator
                      className={cn("my-4", "bg-white", props.separatorClass)}
                      orientation="horizontal"
                    />
                    {!isLoggedIn && (
                      <span
                        className={cn(
                          "absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 flex justify-center items-center py-1 px-2",
                          "bg-black",
                          props.separatorTextClass,
                        )}
                      >
                        {props.separatorText ?? "or"}
                      </span>
                    )}
                  </div>
                )}
                <div className="flex items-center justify-center w-full">
                  <WalletLink
                    className={cn(props.walletButtonClass ?? props.buttonClass)}
                    connectText={props.walletButtonText}
                  />
                </div>
              </>
            )}
          </>
        )}
      </CardContent>
      <CardFooter className="flex justify-center">
        <a href={widgetUrl} rel="nofollow" target="_blank">
          <h4 className="text-white text-center font-bold text-sm">
            {widgetText}
          </h4>
        </a>
      </CardFooter>
    </Card>
  );
};
