import { FC, useState } from "react";
import { cn } from "../utils";
import { Button } from "./Button";
import { Input } from "./Input";
import { useBedrockPassport } from "../hooks/useBedrockPassport";
import { UserProvider } from "../typings/user";

interface Props {
  buttonClass?: string;
}

export const EmailLogin: FC<Props> = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const { signIn, loginCallback } = useBedrockPassport();

  const [error, setError] = useState<string>("");
  const loginWEmail = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(""); // Clear previous errors

    try {
      const res = await signIn(UserProvider.EMAIL, { email, password });

      console.log("EMAIL LOGIN", {
        res,
      });

      if (!res || !res.token) {
        setError("Invalid email or password");
        return;
      }

      const { accessToken, refreshToken } = res.token;

      await loginCallback(accessToken, refreshToken);
    } catch (error) {
      console.error("Login failed:", { error });
      setError(
        error instanceof Error
          ? error.message
          : "Login failed. Please try again.",
      );
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await loginWEmail(e);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {error && <div className="text-red-500">{error}</div>}
      <div className="space-y-2">
        <Input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
        <Input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
      </div>
      <div className="flex gap-2">
        <Button
          type="submit"
          variant="outline"
          className={cn(props.buttonClass)}
        >
          Sign In
        </Button>
      </div>
    </form>
  );
};
